iframe {
  display: none;
}

body {
  margin: 0;
}

.App {
  padding: 0 10px;

  &__content {
    position: relative;
    margin: 0;

    padding: 10px 5px;
  }

  &__tabs {
    padding: 5px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background-color: transparent;
      box-shadow: 0 0 4px #333;
    }
  }

  &__tabs-swicher {
    margin-bottom: 10px;
  }
}
